html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

.main {
  font-family: 'Lato', sans-serif;
  background-image: url("../public/backgroundimage.jpg");
  background-size: 100%;
  background-position: center;
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  flex-basis: 700px;
  margin: 20px;
  padding: 40px;
  background: #02023a;
  border-radius: 20px;
  box-shadow: 0px 0px 9px 0px #090E63;
}

.form__paragraph {
  color: white;
}